// iconfont   // svg  src/icons/svgicons 
<template>
    <i
        :class="className"
        :style="styles"
        @click="_onClick"
    >
        <template v-if="svg !== undefined">
            <Render :render="svg" />
        </template>
    </i>
</template>

<script>
let Render = {
    name: `Render` ,
    functional: true ,
    props: {
        render: Function ,
        color: {
            type: String ,
            default: undefined
        }
    } ,
    render: ( h , ctx ) => {
        return ctx.props.render( h , { _c: h } )
    }
}

export default {
    name: `Icon` ,
    components: { Render } ,
    props: {
        // @doc inline svg
        svg: { type: Function , default: undefined } ,
        // @doc 
        type: { type: String , default: undefined } ,
        // @doc 
        color: { type: String , default: undefined } ,
        // @doc 
        size: { type: Number , default: undefined }
    } ,
    computed: {
        className() {
            let { type , svg } = this
            return [
                type !== undefined ? `bview-iconfont bicon-${type}` : `` ,
                svg !== undefined ? `bview-icon_svg` : ``
            ]
        } ,
        styles() {
            let style = {} ,
                { size , color } = this

            if ( size !== undefined ) {
                style[ `font-size` ] = `${this.size}px`
            }

            if ( color !== undefined ) {
                style.color = this.color
            }

            return style
        }
    } ,
    created() {
        let { type , svg } = this
        if ( type === undefined && svg === undefined ) {
            throw `iconsvgiconfont type`
        }
    } ,
    methods: {
        _onClick( event ) {
            // @doc 
            this.$emit( `click` , event )
        }
    }
}
</script>
