<template>
    <div
        :class="`${b}-input-wrapper`"
        :style="{ width: width }"
    >
        <span
            v-if="$slots.prepend"
            :class="`${b}-input-icon-prepend`"
        >
            <!--  -->
            <slot name="prepend" />
        </span>
        <Icon
            v-if="type === 'password'"
            :svg="eyeIcon"
            :class="`${b}-input-icon`"
            @click="$_handleOpenEye"
        />
        <Icon
            v-if="clearable && currentValue && !disabled"
            :svg="clearIcon"
            :class="clearIconClasses"
            @click="$_handleClear"
        />
        <Icon
            v-else-if="search"
            :svg="searchIcon"
            :class="[`${b}-input-icon`, `${b}-input-search`]"
            @click="$_handleSearch"
        />
        <input
            ref="input"
            :placeholder="placeholder"
            :class="inputClasses"
            :autocomplete="autocomplete"
            :maxlength="maxlength"
            :value="currentValue"
            :autofocus="autofocus"
            :readonly="readonly"
            :disabled="disabled"
            :type="initType"
            v-bind="nativeProps"
            @click="$_clickInput"
            @input="$_handleInput"
            @focus="$_handleFocus"
            @blur="$_handleBlur"
            @keyup.enter="$_handleEnter"
        >
        <span
            v-if="$slots.append"
            :class="`${b}-input-icon-append`"
        >
            <!--  -->
            <slot name="append" />
        </span>
        <!--  -->
        <slot />
    </div>
</template>
<script>
import clearIcon from '../../icons/clear'
import searchIcon from '../../icons/search'
import openEyeIcon from '../../icons/openEye'
import closeEyeIcon from '../../icons/closeEye'
import Icon from '../icon'
import { bviewPrefix as b } from '../../utils/macro'
export default {
    name: `Input` ,
    components: {
        Icon ,
    } ,
    props: {
        //@doc
        type: {
            type: String ,
            default: `text` ,
        } ,
        //@doc
        value: {
            type: [ String , Number ] ,
            default: `` ,
        } ,
        //@doc
        placeholder: {
            type: String ,
            default: undefined ,
        } ,
        //@doconoff
        autocomplete: {
            type: String ,
            default: `off` ,
        } ,
        //@doc
        autofocus: {
            type: Boolean ,
            default: false ,
        } ,
        //@doc
        maxlength: {
            type: Number ,
            default: undefined ,
        } ,
        //@doc
        disabled: {
            type: Boolean ,
            default: false ,
        } ,
        //@doc
        clearable: {
            type: Boolean ,
            default: false ,
        } ,
        //@doc
        readonly: {
            type: Boolean ,
            default: false ,
        } ,
        //@doc
        search: {
            type: Boolean ,
            default: false ,
        } ,
        //@doc
        width: {
            type: String ,
            default: `200px` ,
        } ,
        //@doc
        nativeProps: {
            type: Object ,
            default() {
                return {}
            } ,
        } ,
    } ,
    data() {
        return {
            currentValue: this.value ,
            initType: this.type ,
            clearIcon: clearIcon ,
            searchIcon: searchIcon ,
            openEyeIcon: openEyeIcon ,
            closeEyeIcon: closeEyeIcon ,
            b: b ,
            open: false ,
        }
    } ,

    computed: {
        inputClasses() {
            return [
                `${b}-input` ,
                {
                    [ `${b}-input-disabled` ]: this.disabled ,
                    [ `${b}-input-with-prepend` ]: this.$slots.prepend ,
                    [ `${b}-input-with-append` ]:
                        this.search || this.$slots.append ,
                } ,
            ]
        } ,
        clearIconClasses() {
            return [
                `${b}-input-icon` ,
                {
                    [ `${b}-input-clear-prepend` ]: this.type === `password` ,
                } ,
            ]
        } ,
        eyeIcon() {
            return this.open ? openEyeIcon : closeEyeIcon
        } ,
    } ,
    watch: {
        value( val ) {
            this.$_setCurrentValue( val )
        } ,
    } ,
    methods: {
        $_setCurrentValue( val ) {
            this.currentValue = val
        } ,
        $_handleEnter( event ) {
            if ( this.search && !this.disabled ) {
                //@doc search 
                this.$emit( `search` , event , this.currentValue )
            }
            //@doc enter
            this.$emit( `enter` , event )
        } ,
        $_clickInput( event ) {
            this.$emit( `click` , event )
        } ,
        $_handleInput( event ) {
            let value = event.target.value
            //@doc 
            this.$emit( `input` , value )
            this.$_setCurrentValue( value )
        } ,
        $_handleFocus( event ) {
            //@doc
            this.$emit( `focus` , event )
        } ,
        $_handleBlur( event ) {
            //@doc
            this.$emit( `blur` , event )
            this.$emit( `on-form-blur` , event )
        } ,
        $_handleClear() {
            this.$emit( `input` , `` )
            this.$_setCurrentValue( `` )
        } ,
        $_handleSearch( event ) {
            this.$emit( `search` , event , this.currentValue )
        } ,
        //@doc
        focus() {
            this.$refs.input.focus()
        } ,
        //@doc
        blur() {
            this.$refs.input.blur()
        } ,
        $_handleOpenEye() {
            this.open = !this.open
            this.initType = this.open ? `text` : `password`
        } ,
    } ,
}
</script>
