<template>
    <button
        :disabled="disableStatus"
        :class="buttonClassName"
        @click.prevent="$_onClick"
    >
        <slot />
        <i v-if="loading" />
        <span>{{ tip }}</span>
        <Icon
            v-if="loadingStatus"
            :svg="loadIcon"
            :class="`${b}-switch-loading`"
        />
    </button>
</template>

<script>
import { bviewPrefix as b } from '../../utils/macro'
import loadIcon from '../../icons/loading'
const typeDefault = `primary`
const types = [ `main` , `primary` , `text` ]
const sizes = [ `` , `big` , `small` ]
const SizeDefault = sizes[ 0 ]

export default {
    name: `Button` ,
    props: {
        //@doc
        disabled: {
            type: Boolean ,
            default: false ,
        } ,
        //@docloading
        loading: {
            type: Boolean ,
            default: false ,
        } ,
        //@docslot
        showSlot: {
            type: Boolean ,
            default: false ,
        } ,
        enableTip: {
            type: String ,
            default: `` ,
        } ,
        //@doc
        size: {
            validator( value ) {
                return sizes.indexOf( value ) >= 0
            } ,
            default: SizeDefault ,
        } ,
        //@doc,[primary ,text]
        type: {
            validator( value ) {
                return value ? true : types.indexOf( value ) >= 0
            } ,
            default: typeDefault ,
        } ,
        //@doc
        count: {
            type: Number ,
            default: 30 ,
            loadIcon: loadIcon ,
        } ,
        countingTip: {
            type: String ,
            default: `[count]s` ,
        } ,
    } ,
    data() {
        return {
            b: b ,
            tip: this.enableTip ,
            countNum: this.count ,
            disableStatus: this.disabled ,
            loadingStatus: this.loading ,
        }
    } ,
    computed: {
        buttonClassName() {
            let { type , size } = this

            return [
                `${b}-button` ,
                `${b}-button-size${size}` ,
                `${b}-button-${type}` ,
            ]
        } ,
    } ,
    beforeDestroy() {
        // @NOTE 
        if ( this.sto ) {
            window.clearTimeout( this.sto )
        }
    } ,
    methods: {
        $_onClick( event ) {
            let { loadingStatus } = this
            if ( !loadingStatus ) {
                //@doc event
                this.$emit( `click` , event )
            }
        } ,
        //@doc
        setLock( opt ) {
            let _opt = opt || {}
            this.disableStatus = 1
            // loading
            if ( _opt.loading ) {
                this.loadingStatus = 1
            } else {
                this.loadingStatus = 0
            }
            // 
            if ( _opt.count ) {
                // countingTip
                this.$_countDown()
            } else {
                // disableTip
                this.tip = _opt.tip
            }
        } ,
        //@doc
        unLock() {
            // 
            this.disableStatus = false
            this.tip = this.enableTip
            this.loadingStatus = 0
            clearTimeout( this.timer )
        } ,
        $_countDown: ( function() {
            var _count = 30
            var t = function() {
                this.timer = setTimeout( () => {
                    _count--
                    this.tip = this.countingTip.replace( `[count]` , _count )
                    if ( _count < 0 ) {
                        this.$emit( `countend` , {
                            sender: this ,
                        } )
                        this.unLock()
                        clearTimeout( this.timer )
                        return
                    }
                    t.apply( this )
                } , 1000 )
            }
            return function() {
                this.$emit( `countbegin` , {
                    sender: this ,
                } )
                _count = this.count
                _count--
                this.tip = this.countingTip.replace( `[count]` , _count )
                t.apply( this )
            }
        } )() ,
    } ,
}
</script>
